import React, {  useRef, useState } from 'react';
import io from 'socket.io-client';
import { Box, Button, Typography } from '@mui/material';
import gsap from 'gsap';

import KeyboardVoiceIcon from '@mui/icons-material/KeyboardVoice';
import MicOffIcon from '@mui/icons-material/MicOff';
import audioPlayningImg from './output-onlinegiftools.gif'
import audioReady from './icons8-dot-67.png'
import audioPause from './icons8-pause-64.png'



const WebAudioStreamer = () => {
  const [isRecording, setIsRecording] = useState(false);
  const mediaRecorderRef = useRef(null);
  const audioStreamRef = useRef(null);
   const socket = useRef(null);
     const [messages, setMessages] = useState([]);
     const [input, setInput] = useState('');
     let mediaRecorder;
     const [scriptureContent, setScriptureContent] = useState(null);
     const [prevScripture,setPrevScripture] = useState({book:'',chapter: null, verses: []})
     const [mounted, setmounted] = useState(false)
     const wsProtocol = window.location.protocol === 'https:' ? 'wss' : 'ws';
     const wsHost = window.location.hostname;
     
   
     


   const fetchScripture = async (book,chapter,verses,version="KJV") => {
    const Book= book.charAt(0).toUpperCase() + book.slice(1).toLowerCase()
    
    try {
      const response = await fetch(`/bibles/${version}/${version}_bible.json`);
      if (!response.ok) throw new Error("Bible version not found");

      const bibleData = await response.json();

      if (!bibleData[Book] || !bibleData[Book][chapter]) {
        console.log(`No data found for ${Book} ${chapter} in ${version}`);
        return;
      }

      const scriptureVerses = verses.map(verse => ({
        verse,
        text: bibleData[Book][chapter][verse] || `Verse ${verse} not found`
      }));

      setScriptureContent(scriptureVerses);
      console.log(scriptureVerses)
      
    } catch (err) {
      console.log(err)
      setScriptureContent(null);
    }
  }



  const startRecording = () => {
    setIsRecording(true)
      console.log("WebSocket connection opened.");
      navigator.mediaDevices.getUserMedia({ audio: true })
        .then((stream) => {
          audioStreamRef.current = stream
          mediaRecorder = new MediaRecorder( audioStreamRef.current, { mimeType: "audio/webm" });
  
          mediaRecorder.ondataavailable = (event) => {
            if (event.data.size > 0) {
              event.data.arrayBuffer()
                .then((buffer) => {
                  console.log(`Sending ${buffer.byteLength} bytes of data`);
                  socket.current.readyState === WebSocket.OPEN && socket.current.send(buffer);
                })
                .catch((error) => {
                  console.error("Error reading data as ArrayBuffer:", error);
                });
            }
          };
  
          mediaRecorder.start(1000);
          setIsRecording(true);
        })
        .catch((error) => {
          console.error("Error accessing media devices:", error);
        });
    
  
    
  }

  const stopRecording = () => {
    setIsRecording(false)
    mediaRecorder.stop()
  }

  const mountScripture = () => {
    setmounted(true)
    scripture.current.style.display = 'block'

    gsap.fromTo(scripture.current, {
      opacity: 0,
      y: -50,
      duration: 0
    },{
      opacity: 1,
      y: 0,
      duration: .8,
      ease: 'power1.inOut'
    },
  ) 

  }
  
  const unMountScripture = () => {
    initialState && gsap.to(scripture.current, {
      opacity: 1,
      y: 0,
      duration: 0
    },{
      opacity: 0,
      y: -50,
      duration: .5,
      ease: 'power1.inOut'
    },
  ) 

  }

  const startStreaming = async () => {
    

    

    try {
    /*   const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      mediaRecorderRef.current = new MediaRecorder(stream, { mimeType: 'audio/webm' });

      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          socket.emit('audio', event.data);
        }
      };

      mediaRecorderRef.current.start(300); // Send data every 300ms
      setIsRecording(true); */
      
      socket.current = new WebSocket(`${wsProtocol}://${wsHost}`)
    
      socket.current.onopen = () => {
        console.log('WebSocket connection opened.');
        // Optionally, send an initial message
        
      };

      socket.current.onmessage = (event) => {
        const data = JSON.parse(event.data)
        console.log(data)
  
        if (data.status !== 200){
          console.log('stopRecording()')
          stopStreaming()
          
          if(data.message?.error?.status === 'RESOURCE_EXHAUSTED'){
            alert("Gemini tokens exhausted")
          }
        }else{
          if( data.type === 'text'){
            startRecording()
            

          }else if(data.type === 'application/json'){
            setMessages(data.message[0])

            const { book, chapter, verses } = data.message[0] 

            if (book && Number.isInteger(chapter) && verses.length > 0) {

              const isNewScripture =
              prevScripture.book !== book ||
              prevScripture.chapter !== chapter ||
              prevScripture.verses !== verses

              fetchScripture(book, chapter, verses);
              !mounted && mountScripture();
          
              // Update the previously mounted scripture reference
              setPrevScripture({ book, chapter, verses });

              if (isNewScripture) {
                setmounted(false)
              }
            }
          }
        }
        /* console.log('Message received:', event.data);
        setMessages((prev) => [...prev, event.data]); */
      };
  
      socket.current.onerror = (error) => {
        console.error('WebSocket error:', error);
      };
  
      socket.current.onclose = () => {
        console.log('WebSocket connection closed.');
        stopStreaming()
      };
  
      

 

    } catch (error) {
      console.error('Failed to start audio stream:', error);
    }
  };

  const stopStreaming = () => {
    setInitialState(false)
    /* if (mediaRecorderRef.current) {
      mediaRecorderRef.current.stop();
      mediaRecorderRef.current.stream.getTracks().forEach((track) => track.stop());
      
    } */
      if (mediaRecorder) {
        console.log("stopping media recorder")
        mediaRecorder.stop();
        mediaRecorder = null;
      }


      if (audioStreamRef.current) {
        audioStreamRef.current.getTracks().forEach(track => track.stop());
        audioStreamRef.current = null;
      }


      if (socket.current && socket.current.readyState === WebSocket.OPEN) {
        socket.current.close()
        console.log("socket closed")
      }
      setIsRecording(false)
  };
  const [initialState, setInitialState] = useState(true)
  const scripture = useRef(null)
 
 

  
  

  return (
    <Box sx={{bgcolor:'#ECECEC', width: '100%' ,height:'100vh', display:'flex', flexDirection:'column'}}>
      <Box >
        <Typography sx={{fontWeight:{xs:'500',md:'700'}, fontSize:'16px', textAlign:'center', pt:'24px',mb:'1em', fontFamily:'Geist'}}>VerseCatch</Typography>
      </Box>
      <Box sx={{mx:{xs:'24px',md:'341px'}, height:'100%', display:'flex',flexDirection:'column', justifyContent:'center', alignItems:'center'}}>
        <Box ref={scripture} sx={{display:'none' }}>
        <Typography sx={{fontWeight:'700', fontSize:'20px', textAlign:'center', fontFamily:'Geist'}}>
        {messages && `${messages.book} ${messages.chapter}:${messages.verses}`}
        </Typography>
        <Typography sx={{ fontWeight:'400', fontSize:'20px', lineHeight:'26px', textAlign:'center', fontFamily:'Geist'}}>
        {scriptureContent?.map((verse) => (
          <Typography>
            <Typography sx={{display:'inline', fontWeight:'600', fontSize:'18px'}}>{verse.verse} </Typography>
            {verse.text}
            </Typography>
        ))}
        </Typography>
        </Box>
      </Box>
      <Box sx={{bgcolor:'#F7F7F7' , width:{xs: '100%',md:'50%'},display:'flex', justifyContent:'center', alignItems:'center', flexDirection:'column', py:'16px',mx:'auto', borderRadius:{md:'24px',xs:'24px 24px 0 0'}, mb:{md:'1em'}, mt:'1em', paddingTop:{xs:'2em'}, position:'relative'}}>
        <Box sx={{ width:'32px', height:'4px', background:'#D9D9D9', position:'absolute', top:'8px', left:'auto', display:{xs:'block', md:'none'}}}></Box>
        <Box sx={{width: '60px', height:'60px', background:'#E8E8E8', display:'flex', justifyContent:'center', alignItems:'center', borderRadius:'50%', mixBlendMode:'multiply'}}>
        <img width="24" height="24" src={ !isRecording?( initialState? audioReady: audioPause): audioPlayningImg} alt="external-Dot-atm-others-inmotus-design" style={{ objectFit:'contain'}} />
        </Box>
        <Typography sx={{ px:'94px', py:'16px',textAlign:'center', fontFamily:'Geist'}}>Transcribing and detecting Bible quotations in real time.</Typography>
        <Box sx={{width:'100%', display:'flex', justifyContent:'center', alignItems:'center', pb:'16px', transition:'.3s ease-in-out'}}><Button startIcon={isRecording? <MicOffIcon/> :<KeyboardVoiceIcon/>} color="white" sx={{ background:isRecording? '#FFDCDB':'black', color:isRecording? '#FF6259': 'white', justifyContent:'center', alignItems:'center', height:'48px', borderRadius: '48px', p: '14px 28px', fontFamily:'Geist'}} onClick={isRecording? stopStreaming : startStreaming}>{isRecording? "Stop Listening": initialState? "Start Listening": "Continue Listening"}</Button></Box>
      </Box>

    </Box>
  );
};

export default WebAudioStreamer;
